<script setup lang="ts">
import { ref } from 'vue'

import VButton from '~/ui/VButton.vue'

const donates = [
  { amount: 1 },
  { amount: 2 },
  { amount: 5 },
  { amount: 10 },
  { amount: 20 },
  { amount: 50 },
]

const donateAmount = ref(0)

const decrease = () => {
  const amount = donateAmount.value - 10

  if (amount <= 0) {
    donateAmount.value = 0
  } else {
    donateAmount.value = amount
  }
}

const increase = (count: number) => {
  donateAmount.value += count
}
</script>

<template>
  <div class="donate">
    <h3 class="donate__title">
      {{ $t('main.support.donate.title') }}
    </h3>
    <div class="donate__select">
      {{ $t('main.support.donate.currency') }}
    </div>

    <div class="donate-tabs">
      <div class="donate-tabs__item">
        {{ $t('main.support.donate.frequency') }}
      </div>
    </div>

    <div class="donate-amount">
      <div class="donate-amount__title">
        {{ $t('main.support.donate.subtitle') }}
      </div>
      <div class="donate-amount-calc">
        <button class="donate-amount-calc__minus" @click="decrease()" />
        <span class="donate-amount-calc__num text-montserrat text-weight-600">
          {{ donateAmount }} $
        </span>
        <button
          class="donate-amount-calc__plus"
          @click="increase(donates[3].amount)"
        />
      </div>
      <div class="donate-amount-list">
        <button
          v-for="donate in donates"
          :key="donate.amount"
          class="donate-amount-list__item"
          @click="increase(donate.amount)"
        >
          +{{ donate.amount }} $
        </button>
      </div>
    </div>

    <VButton>
      <NuxtImg
        src="/images/icons/icon-heart.svg"
        alt="Heart"
        width="24"
        height="24"
      />
      <span class="text-capitalize text-weight-600">
        {{ $t('main.support.donate.button') }}
      </span>
    </VButton>
  </div>
</template>

