<script setup lang="ts">
import { ref, computed } from 'vue'

const { id, title, text } = defineProps<{
  id: number
  title: string
  text: string
}>()

const activeItem = ref(id === 1)

const count = computed(() => id.toString().padStart(2, '0'))
</script>

<template>
  <div class="faq-item" :class="{ active: activeItem }">
    <div
      class="faq-item__title"
      :class="{ active: activeItem }"
      @click="activeItem = !activeItem"
    >
      <span class="faq-item__num text-montserrat">
        {{ count }}
      </span>
      <h4>
        {{ title }}
      </h4>
      <span class="faq-item__close" />
      <span class="icon-chevron" />
    </div>
    <p v-if="activeItem">
      {{ text }}
    </p>
  </div>
</template>

